/* screen
	----------------------------------- */
.screen {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: relative;
	border-top: 1px solid rgba(#5facae, .3);
	height: calc(100vh - 190px);
	@include r(1439) {
		height: calc(100vh - 161px); }
	@include md {
		height: auto;
		min-height: 600px; }
	@include xs {
		justify-content: flex-start; }
	&__container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		position: relative;
		max-width: 1158px;
		min-height: 450px;
		margin: -15px 0 0;
		width: 100%;
		z-index: 1;
		@include md {
			max-width: 920px; }
		@include xs {
			margin: 0;
			padding: 15px 0 40px; } }
	&__pic {
		@include coverdiv;
		@include cover;
		z-index: 0;
		background-color: #EDEBED;
		@include md {
			background-size: 145% 100%;
			background-position: 49% 50%; }
		@include sm {
			background-size: 226% 100%;
			background-position: 17% 50%; }
		@include xs {
			background-size: cover;
			background-position: 20% 50%; }
		&_double-bg {
			background-position: left center, right center;
			background-size: 50%, 50%;
			z-index: -1; } }
	&__slider {
		width: 100%;
		padding: 0 30px;
		display: none;
		@include sm {
			max-width: 800px;
			margin: 0 auto; }
		@include xs {
			padding: 0; }
		&.slick-initialized {
			display: block; }
		.screen__arrow-next.slick-disabled,
		.screen__arrow-prev.slick-disabled {
			@include md {
				fill: rgba($accent, .8); }
			@include md {
				fill: rgba(#fff, .3); } }
		.screen__arrow-next,
		.screen__arrow-prev {
			@include sm {
				fill: darken($accent, 5); } }
		.screen__arrow-prev {
			@include xs {
				left: -7px; } }
		.screen__arrow-next {
			@include xs {
				right: -7px; } }
		.slick-arrow {
			@include xs {
				top: 88px;
				fill: #fff; }
			@include r(374) {
				top: 34.5%; } } }

	&__arrow {
		transition: fill .25s;
		background-color: transparent;
		fill: rgba($accent, .6);
		position: absolute;
		top: calc(50% - 23px);
		z-index: 2;
		&.slick-disabled {
			fill: rgba(#fff, .6);
			&:hover {
				fill: rgba(#fff, .6); } }
		&:hover {
			fill: $accent; }
		.arrow-icon {
			width: 42px;
			height: 45px; }
		&-prev {
			left: 0; }
		&-next {
			right: 0;
			.arrow-icon {
				@include rotate(180); } } }
	&__text {
		text-align: center;
		margin: 0 auto; }
	&__descr {
		@include sm {
			max-width: 600px;
			margin: 0 auto;
			padding: 10px 30px;
			background-color: rgba($accent, .9);
			color: #fff;
			@include f('ub-r');
			border-radius: 10px; } }

	&__title {
		max-width: 600px;
		margin: 0 auto 15px;
		font-weight: 300;
		font-size: 32px;
		text-transform: uppercase;
		@include sm {
			font-size: 28px; }
		@include xs {
			padding: 0 10px;
			font-size: 20px;
			color: #000; }
		&_margin {
			margin: 0 auto 35px; } }
	&__adv {
		display: flex;
		justify-content: center;
		@include md {
			margin: 20px 0 0; }
		@include xs {
			margin-top: 10px;
			justify-content: space-between; }
		&-item {
			display: flex;
			align-items: center;
			justify-content: center;
			overflow: hidden;
			width: 118px;
			height: 118px;
			border-radius: 50%;
			border: 3px solid #ffffff;
			color: #fff;
			text-align: center;
			& + & {
				margin-left: 40px;
				@include xs {
					margin-left: 0; } } }
		&-inn {
			padding: 10px;
			height: 108px;
			width: 108px;
			border-radius: 50%;
			background-color: $accent;
			&_mod {
				padding-top: 30px;
				line-height: 1.1; } }
		&-count {
			@include f('bn-r',39);
			line-height: 30px; }
		&-text {
			font-size: 12px; }
		&-text_lg {
			font-size: 15px; } }
	&__icon {
		position: absolute;
		z-index: 1;
		border-radius: 50%;
		left: calc(50% - 14px);
		bottom: 17px;
		width: 28px;
		height: 28px;
		background-color: #fff;
		@include xs {
			bottom: 6px; }
		&::after {
			@include vcenter;
			content: '';
			border-radius: 50%;
			@include s('scroll'); } } }

/* advice
	----------------------------------- */
.advice {
	&__form-inn {
		display: flex;
		justify-content: center;
		align-items: center;
		@include sm {
			flex-direction: column;
			align-items: stretch;
			padding: 0 100px; }
		@include xs {
			padding: 0 20px; } }
	&__form-field {
		flex-basis: (202/1068 * 100%);
		@include md {
			flex-basis: 25%; }
		@include sm {
			margin-bottom: 10px; }
		& + & {
			margin-left: 15px;
			@include sm {
				margin-left: 0; } } }
	&__btn {
		min-width: 256px;
		height: 45px;
		line-height: 45px;
		@include sm {
			width: 100%; } }

	&_screen {
		margin: 55px 0 75px;
		@include r(1439) {
			margin: 30px 0 45px; }
		@include sm {
			margin: 30px 0 0; }
		@include xs {
			margin: 20px 0 0; } } }
